import React, { Component } from 'react';

import styles from './pointBox.module.scss';

class PointBox extends Component {
  state = {}

  static defaultProps = {
    value: 4.5
  }

  renderBox = () => {
    let box = []
    for (let i = 1; i <= 5; i++) {
      const isNotFullBox = (i) - (this.props.value - (this.props.value % 1)) === 1
      box = [
        ...box,
        <div key={i} className={styles.boxBody}>
          <div
            className={styles.boxValue}
            style={{
              width: this.props.value >= i
                ? '100%'
                : isNotFullBox ? `${(this.props.value % 1) * 100}%` : '0%'
            }}> </div>
        </div >
      ]
    }
    return box
  }

  render() {
    return (
      <div className={styles.pointBoxWrapper}>
        {this.renderBox()}
      </div>
    );
  }
}

export default PointBox;