import React, { Component } from 'react';

import './footer.module.scss';

class Footer extends Component {
  state = {
    startYear: 2018,
    latestYear: 2018,
  }

  componentDidMount() {
    const today = new Date()
    const year = today.getFullYear()
    this.setState({ latestYear: year })
  }

  render() {
    const { startYear, latestYear } = this.state
    return (
      <footer>
        <div>{`Â© 2018 ${startYear !== latestYear ? latestYear : ''} akiizudev.com`}</div>
        <div>Built by Vitaya Jealwarakun</div>
      </footer>
    );
  }
}

export default Footer;